.page-header {
  max-height: 264px;
}

.page-header__wrapper {
  width: calc(100% - 40px);
  min-width: 320px;
  margin: 0 auto;
  padding: 0;
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid @special_gainsboro;

  @media (min-width: @tablet-width) {
    display: flex;
    justify-content: space-between;
    width: calc(100% - 60px);
    max-width: 1220px;
    border-bottom: none;
  }

  @media (min-width: @desktop-width) {
    width: calc(100% - 220px);
    max-width: 1440px;
  }
}

.page-header__logo {
  height: 65px;
  padding: 0 0 0 20px;

  &:hover,
  &:focus {
    opacity: 0.8;
  }

  &:active {
    opacity: 0.6;
  }

  @media (min-width: @tablet-width) {
    height: 100px;
    padding: 0;
  }

  @media (min-width: @desktop-width) {
    height: 160px;
    z-index: 5;
  }
}

.page-header__logo .logo__image-text {
  width: 101px;

  @media (min-width: @tablet-width) {
    display: none;
  }
}
