.slider {
  width: 100%;
  margin: 0 auto;

  @media (min-width: @tablet-width) {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
  }
}

.slider__list {
  margin-bottom: 17px;

  @media (min-width: @tablet-width) {
    width: 683px;
    height: 520px;
    position: relative;
    margin: -183px auto 18px;
  }

  @media (min-width: @desktop-width) {
    width: 98%;
    margin: -185px 0 27px;
  }
}

.slider__item {
  @media (min-width: @tablet-width) {
    position: absolute;
    width: 100%;
    height: 520px;
    overflow: hidden;
    z-index: 3;
  }

  @media (min-width: @desktop-width) {
    height: 664px;
  }
}

.slider__item--before {
  display: block;
  width: 100%;
  margin-bottom: 32px;

  @media (min-width: @tablet-width) {
    width: 50%;
    background-color: @special_smoke-white;
    background-image: linear-gradient(to bottom, @basic_white 182px, @special_smoke-white 183px);
  }

  @media (min-width: @desktop-width) {
    width: 57%;
    bottom: -124px;
    margin-bottom: 0;
    background-color: @special_smoke-grey;
    background-image: linear-gradient(to bottom, @basic_white 205px, @special_smoke-grey 137px);
  }
}

.slider__item--after {
  display: none;
  margin: 0 32px 24px 37px;

  @media (min-width: @tablet-width) {
    display: block;
    width: 100%;
    right: -63px;
    z-index: 2;
    background-color: @special_smoke-white;
    background-image: linear-gradient(to bottom, @basic_white 182px, @special_smoke-white 183px);
  }

  @media (min-width: @desktop-width) {
    background-image: linear-gradient(to bottom, @basic_white 205px, @special_smoke-white 137px);
    left: 57px;
    bottom: -124px;
    width: 545px;
    margin-bottom: 0;
  }
}

.slider__img {
  max-width: 100%;
  display: flex;
  margin: 0 auto;
  padding-right: 10px;

  @media (min-width: @tablet-width) {
    display: block;
    margin: 10px auto;
    width: 685px;
    height: 520px;
    max-width: none;
    background-size: 690px 517px;
  }
}

.slider__img--after {
  @media (min-width: @tablet-width) {
    margin: 10px auto;
    width: 540px;
    height: 520px;
    background-size: 690px 517px;
  }
}

.slider__range {
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 10;

  @media (min-width: @tablet-width) {
    width: 80%;
    max-width: 1220px;
    margin: 0 auto;
    padding-top: 6px;
  }

  @media (min-width: @desktop-width) {
    width: 100%;
    padding: 0 38px 0 43px;
  }
}

.slider__button {
  margin: 0 15px 0 20px;
  padding: 0;
  .font(@Oswald; normal; 400);
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  text-align: center;
  color: @basic_black;
  background-color: inherit;
  border: none;

  @media (min-width: @tablet-width) {
    margin: 0 22px 0 21px;
    font-size: 20px;
    line-height: 30px;
    border-bottom: 1px dotted rgba(0, 0, 0, 0.24);
  }

  @media (min-width: @desktop-width) {
    margin: 0 27px 0 21px;
  }

  &:hover,
  &:focus {
    border-bottom: 1px solid rgba(0, 0, 0, 0.4);
  }

  &:active {
    border-bottom: none;
    margin-bottom: 1px;
  }
}

.slider__button--active {
  @media (min-width: @tablet-width) {
    border-bottom: 1px solid rgba(0, 0, 0, 0.4);
  }
}

.slider__input-range {
  flex-grow: 0;
  width: 84px;
  height: 14px;
  margin: 0;
  appearance: none;
  box-sizing: border-box;
  background: @basic_white;
  border: 1px solid @special_light-grey;
  border-radius: 6px;
  outline: none;
  z-index: 10;

  &::-ms-track {
    width: 100%;
    height: 6px;
    margin: 0 6px;
    cursor: pointer;
    background: @basic_white;
    border-radius: 6px;
    border: none;

    @media (min-width: @tablet-width) {
      width: 70%;
      background: @special_light-grey;
    }

    @media (min-width: @desktop-width) {
      width: 65%;
    }
  }

  &::-webkit-slider-runnable-track {
    width: 100%;
    height: 6px;
    margin: 0 6px;
    cursor: pointer;
    background-color: @basic_green;
    background-position: left center;
    border-radius: 6px;
    border: none;

    @media (min-width: @tablet-width) {
      width: 70%;
      background: @special_light-grey;
    }

    @media (min-width: @desktop-width) {
      width: 65%;
    }
  }

  &::-moz-range-track {
    width: 90%;
    height: 6px;
    margin: 0 6px;
    cursor: pointer;
    background-color: @basic_green;
    background-position: left center;
    border-radius: 6px;
    border: none;

    @media (min-width: @tablet-width) {
      width: 70%;
      background: @special_light-grey;
    }

    @media (min-width: @desktop-width) {
      width: 65%;
    }
  }

  &::-ms-fill-lower {
    background: @basic_green;
    border-radius: 6px;
    border: none;

    @media (min-width: @tablet-width) {
      background: @special_light-grey;
    }
  }

  &::-ms-fill-upper {
    background: @basic_green;
    border-radius: 6px;
    border: none;

    @media (min-width: @tablet-width) {
      background: @special_light-grey;
    }
  }

  &::-webkit-slider-thumb {
    appearance: none;
    width: 8px;
    height: 6px;
    background: @basic_green;
    border: none;
    box-sizing: border-box;
    border-radius: 50%;
    filter: drop-shadow(0 1px 3px rgba(0, 0, 0, 0.16));

    @media (min-width: @tablet-width) {
      width: 34px;
      height: 34px;
      margin-top: -14px;
      background: @basic_green;
      border: 1px solid @special_light-grey;
      box-shadow: rgb(255, 255, 255) 0 0 0 12px inset;
    }
  }

  &::-moz-range-thumb {
    appearance: none;
    width: 8px;
    height: 6px;
    background: @basic_green;
    border: none;
    box-sizing: border-box;
    border-radius: 50%;
    filter: drop-shadow(0 1px 3px rgba(0, 0, 0, 0.16));

    @media (min-width: @tablet-width) {
      width: 34px;
      height: 34px;
      background: @basic_green;
      border: 1px solid @special_light-grey;
      box-shadow: rgb(255, 255, 255) 0 0 0 12px inset;
    }
  }

  &::-ms-thumb {
    appearance: none;
    width: 8px;
    height: 6px;
    background: @basic_green;
    border: none;
    box-sizing: border-box;
    border-radius: 50%;
    filter: drop-shadow(0 1px 3px rgba(0, 0, 0, 0.16));

    @media (min-width: @tablet-width) {
      width: 34px;
      height: 34px;
      background: @basic_green;
      border: 1px solid @special_light-grey;
      box-shadow: rgb(255, 255, 255) 0 0 0 12px inset;
    }
  }

  @media (min-width: @tablet-width) {
    width: 70%;
    height: 6px;
    background: @special_light-grey;
  }

  @media (min-width: @desktop-width) {
    width: 65%;
  }

  &::-ms-thumb:hover,
  &::-ms-thumb:focus {
    filter: drop-shadow(0 2px 4px rgba(0, 0, 0, 0.19));
  }

  &::-moz-range-thumb:hover,
  &::-moz-range-thumb:focus {
    filter: drop-shadow(0 2px 4px rgba(0, 0, 0, 0.19));
  }

  &::-webkit-slider-thumb:hover,
  &::-webkit-slider-thumb:focus {
    filter: drop-shadow(0 2px 4px rgba(0, 0, 0, 0.19));
  }

  &::-ms-thumb:active {
    border: 1px solid @basic_green;
    filter: drop-shadow(0 3px 5px rgba(0, 0, 0, 0.2));
  }

  &::-moz-range-thumb:active {
    border: 1px solid @basic_green;
    filter: drop-shadow(0 3px 5px rgba(0, 0, 0, 0.2));
  }

  &::-webkit-slider-thumb:active {
    border: 1px solid @basic_green;
    filter: drop-shadow(0 3px 5px rgba(0, 0, 0, 0.2));
  }
}
